import React, { useState } from 'react';
import { Link } from 'react-router-dom';

import './Join.css';

const Join = () => {
  const [name, setName] = useState('')
  const [room, setRoom] = useState('')

  const changeName = (event) => setName(event.target.value)
  const changeRoom = (event) => setRoom(event.target.value)

  return (
    <div className="joinOuterContainer">
      <div className="joinInnerContainer">
        <h1 className="heading">Join</h1>
        <div><input placeholder="Name" className="joinInput" type="text" onChange={changeName} /></div>
        <div><input placeholder="Room" className="joinInput mt-20" type="text" onChange={changeRoom} /></div>
        <Link onClick={event => (!name || !room) ? event.preventDefault() : null } to={`/chat?name=${name}&room=${room}`}>
          <button className="button mt-20" type="submit">Sign In</button>
        </Link>
      </div>
    </div>
  );
}

export default Join;
